	align-items: center;
	justify-content: center;
	position: relative;
	&:after {
		content: '';
		width: 1px;
		height: 8.59375vw;
		background-color: #000;
		display: inline;
		position: absolute;
		right: 0; }
	&:last-child {
		&:after {
