//Button to up
.go-to-up {
	width: 2.8125vw;
	height: 2.8125vw;
	background-color: #fff;
	border: 1px solid $main-color;
	box-shadow: 0 4px 10px rgba(0, 0, 0, 0.25);
	border-radius: 50%;
	z-index: 10;
	position: fixed;
	right: 3.80208333333vw;
	bottom: 3.80208333333vw;
	visibility: hidden;
	opacity: 0;
	transition: visibility 0.65s, opacity 0.65s;
	& a {
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		.arrow-go-to-up {
			width: 1.25vw;
			height: 1.25vw;
			border-right: 1px solid $main-color;
			border-top: 1px solid $main-color;
			transform: rotate(-45deg);
			position: absolute;
			bottom: 15%; } } }

.go-to-up__visible {
	opacity: 0.5;
	visibility: visible;
	&:hover {
		opacity: 1; } }



.slides-wrapper {
	width: 100%;
	height: 100%;
	position: relative; }

.next-slides-wrapper {
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%; }

.send-error, .send-successful {
	background-color: rgba(0, 0, 0, 0.8);
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	z-index: 20;
	display: none;
	.window {
		width: 26.0416666667vw;
		height: 7.8125vw;
		margin: 0 auto;
		margin-top: 10.3645833333vw;
		background: #fff;
		display: flex;
		position: relative;
		justify-content: center;
		padding-top: 3.64583333333vw;
		font-family: 'Bahnschrift', sans-serif;
		font-size: 3rem;
		.close {
			position: absolute;
			right: 1.5625vw;
			bottom: 1.5625vw;
			background-color: #F9F9F9;
			border-radius: 0;
			border: none;
			width: 6.77083333333vw;
			height: 1.82291666667vw;
			cursor: pointer;
			font-size: 2rem;
			transition: box-shadow 0.65s;
			&:hover {
				box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15); } } } }
.send-error {
	.window {
		color: $error;
		.possible-reason {
			position: absolute;
			bottom: 1.5625vw;
			left: 1.5625vw;
			font-size: 1rem;
			width: 10.3645833333vw; } } }
.send-successful {
	.window {
		color: $main-color; } }
.errors {
	position: absolute;
	right: 11.9791666667vw;
	top: 5.20833333333vw;
	background: #fff;
	width: 16.667vw;
	font-size: 3rem;
	line-height: 6rem;
	color: #1e1e1e;
	font-family: 'Bahnschrift', sans-serif;
	box-shadow: 0 4px 25px rgba(0, 0, 0, 0.15);
	.error {
		display: flex;
		align-items: center;
		&:before {
			content: '';
			width: 0.78125vw;
			height: 0.78125vw;
			border-radius: 50%;
			background-color: $error;
			display: inline-block;
			position: relative;
			margin: 0 10px; } } }


.preloader {
	height: 100%; }
.load-line {
	opacity: 1;
	transition: opacity 0.65s;
	width: 10.3645833333vw;
	height: 7px;
	background-color: $body-bg-color;
	position: absolute;
	top: calc(50vh + 60px);
	left: 50%;
	margin-right: -50%;
	transform: translateX(-50%); }
.progress {
	position: absolute;
	left: 0;
	height: 100%;
	background-color: $main-color; }




